body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* .center-align {
    text-align:center;
    align-content: center;
} */
html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Source Code Pro', monospace;
    background-color:#4ABDAC;
    color:white;
    /* scroll-behavior: smooth; */
}



a {
    color:#CAEBF2;
}

a:hover {
    color:#F7B733;
}

.arrow {
    display: none;
}

.navbar {
    font-size:16pt;
}

.card {
    background-color:#4ABDAC;
    margin-top:5%;
    margin-bottom:5vh;
    border:none;
}

.center-align {
    text-align:center;
    align-content: center;
}

.input {
    padding:2%;
    border:solid 0.5px;
    border-radius: 10px;
}

.input code {
    color:white;
}

.output {
    padding:2%;
    border:solid 0.5px;
    border-radius: 10px;
    background-color:#f0f5f5;
}

@media only screen and (min-width:600px) {
    .right {
        float:right;
    }

    .arrow {
        color: #6D7993;
        display:inline;
    }
    
    .arrow:hover {
        color:#F7B733;
    }

    .card:not(.seventeenth) {
        background-color:#4ABDAC;
        margin-top:5%;
        margin-bottom:55vh;
        border:none;
    }
    .seventeenth {
        background-color:#4ABDAC;
        margin-top:5%;
        margin-bottom:25vh;
        border:none;
    }
}

.App {
  text-align: left;
}

.scroll {
  scroll-behavior: smooth;
}


